export class FilterRolesValueConverter {
  toView(value, array) {
    if(value && array){
		var filteredArray = value.filter(item =>{
			return array.indexOf(item.role) === -1;
		});
		if(filteredArray.length === 0) filteredArray.push({role: "NOROLE", label: "No Roles Remaining"});
		return filteredArray;
	}
    return value;
  }

}